@import url("https://fonts.googleapis.com/css?family=Quicksand");
.desktop__headers {
  display: none; }

.panel {
  width: 100%;
  background-color: #ECECEC;
  text-align: center;
  height: 140px; }

#forms {
  display: -ms-flexbox;
  display: flex; }

form {
  width: 50%; }

#searchRestaurant {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 80%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px; }

.mobileButton {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 20%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px;
  padding-left: 5px; }

#myLocation {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 80%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px; }

#selectRating {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 16%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px;
  float: left; }

#max-duration, #transit {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 22%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px;
  float: left; }

nav {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  width: 40%;
  font: bold 16px "Quicksand", sans-serif;
  padding-left: 10px;
  float: left;
  padding-left: 0; }
  nav select {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 40px;
    width: 100%;
    font: bold 16px "Quicksand", sans-serif;
    padding-left: 10px; }
  nav ul {
    display: none; }

#openNow {
  padding-left: 0;
  font-size: 12px;
  margin: 10px 0; }

.desktopButtonTitle {
  display: none; }

.title {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
  width: 100%;
  background-color: #F78888;
  color: white;
  margin: 0 auto;
  padding: 10px 50px;
  font-size: 32px; }

nav ul {
  height: 200px;
  width: 18%;
  overflow: hidden;
  overflow-y: scroll; }

/* ====================== Horizontal Views ====================== */
@media screen and (min-width: 769px) {
  .desktopButtonTitle {
    display: block; }
  .mobileButtonTitle {
    display: none; }
  .desktop__headers {
    text-align: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-left: 5%;
    display: block;
    width: 100%;
    font: 900 2vw "Quicksand", sans-serif;
    margin-bottom: 5px; }
  .panel {
    width: 35%;
    height: 100vh; }
  .title {
    font-size: 3vw; }
  #myLocation {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 60%;
    border-style: solid;
    border-color: #F78888;
    margin: 0 auto 4% 5%;
    font: bold 1.5vw "Quicksand", sans-serif;
    padding-left: 5px;
    border-radius: 15px;
    border-width: 3px;
    float: left; }
  #locationSubmit {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 25%;
    color: white;
    font: bold 2vw "Quicksand", sans-serif;
    float: right;
    border-radius: 15px;
    background-color: #F78888;
    -webkit-box-shadow: 0 10px 6px -6px #777777;
    box-shadow: 0 10px 6px -6px #777777;
    text-shadow: 1px 2px #777777;
    margin: 0 5% 20px auto;
    margin-bottom: 10%; }
  #searchSubmit {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 25%;
    color: white;
    font: bold 2vw "Quicksand", sans-serif;
    float: right;
    border-radius: 15px;
    background-color: #F3D250;
    -webkit-box-shadow: 0 10px 6px -6px #777777;
    box-shadow: 0 10px 6px -6px #777777;
    text-shadow: 1px 2px #777777;
    margin: 0 5% 20px auto;
    margin-bottom: 10%; }
  #searchRestaurant {
    float: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 60%;
    border-style: solid;
    border-color: #F3D250;
    margin: 0 auto 4% 5%;
    font: bold 1.5vw "Quicksand", sans-serif;
    padding-left: 5px;
    border-radius: 15px;
    border-width: 3px; }
  #selectRating {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 22%;
    border-style: solid;
    border-color: #5DA2D5;
    margin: 0 auto 4% 5%;
    font: bold 1.5vw "Quicksand", sans-serif;
    padding-left: 5px;
    border-radius: 15px;
    border-width: 3px;
    height: 45px;
    margin: 0 5% 4% 5%; }
  #transit, #max-duration {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 10%;
    width: 29%;
    border-style: solid;
    border-color: #5DA2D5;
    margin: 0 auto 4% 5%;
    font: bold 1.5vw "Quicksand", sans-serif;
    padding-left: 5px;
    border-radius: 15px;
    border-width: 3px;
    height: 45px;
    margin: 0 5% 4% 0; }
  #forms {
    display: block; }
  form {
    width: 100%; }
  nav {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 80%;
    margin: 0 15% 0 5%;
    height: 18%; }
    nav select {
      display: none; }
    nav ul {
      display: block;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      height: 10%;
      width: 100%;
      border-style: solid;
      border-color: #5DA2D5;
      margin: 0 auto 4% 5%;
      font: bold 1.5vw "Quicksand", sans-serif;
      padding-left: 5px;
      border-radius: 15px;
      border-width: 3px;
      margin-left: 0;
      height: 100%; }
      nav ul li {
        text-align: left;
        padding-left: 5px;
        border-radius: 5px; }
        nav ul li:hover {
          background-color: #90CCF4; } }



